import React, { Component, Fragment } from "react";
import axios from "./axios";
import ZingChart from "zingchart-react";
import { PieChart } from "react-easy-chart";

class Downloads extends Component {
  state = {
    data: [],
    lineChartData: [],
  };

  componentDidMount() {
    var slug = localStorage.getItem("slug");
    const proxyurl = "https://cors-anywhere.herokuapp.com/";
    const url =
      "http://api.wordpress.org/stats/plugin/1.0/downloads.php?slug=" +
      slug +
      "&limit=20";
    axios.get(proxyurl + url).then((res) => {
      this.setState({ data: res.data });
    });
  }

  constructor(props) {
    super(props);

    this.state = {
      config: {
        type: "area",
        title: {
          text: "Daily download",
          "font-size": "14",
          "text-align": "left",
        },
        plot: {
          aspect: "spline",
        },
      },
      plotarea: { margin: "dynamic" },
    };
  }

  render() {
    var d = this.state.data;
    if (!d) return <p className="loading"></p>;

    const df = Object.values(d);
    //console.log(df);

    var mySeries = [{ values: df }];
    //============================
    let n;
    for (let [key, value] of Object.entries(d)) {
      const d = new Date(key);
      const ye = new Intl.DateTimeFormat("en", { year: "2-digit" }).format(d);
      const mo = new Intl.DateTimeFormat("en", { month: "short" }).format(d);
      const da = new Intl.DateTimeFormat("en", { day: "2-digit" }).format(d);
      const formatDate = `${da}-${mo}-${ye}`;
      const strDate = JSON.stringify(formatDate);
      // eslint-disable-next-line no-useless-concat
      n = `x:` + strDate + "," + `y:${value}`;
      //console.log(n);
    }

    return (
      <Fragment>
        <ZingChart
          data={this.state.config}
          series={mySeries}
          height="300"
          width="100%"
        />

        <PieChart
          data={[
            { key: "A", value: 100 },
            { key: "B", value: 200 },
            { key: "C", value: 50 },
          ]}
        />
      </Fragment>
    );
  }
}
export default Downloads;
